.raised-card-project {
  background: #09252e;
  background-image: linear-gradient(
    rgba(255, 255, 255, 0.1),
    rgba(255, 255, 255, 0)
  );
  border-radius: 0.25rem;
  border: 1px solid rgba(0, 0, 0, 0.8);
  box-shadow: inset 0px 1px 0px rgba(255, 255, 255, 0.2),
    inset 0px -1px 0px rgba(255, 255, 255, 0.1),
    inset 1px 0px 0px rgba(255, 255, 255, 0.05),
    inset -1px 0px 0px rgba(255, 255, 255, 0.05), 0 4px 8px rgba(0, 0, 0, 0.3);
  cursor: default;
  display: inline-block;
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.2;
  margin: 0;
  margin-bottom: 1rem;
  padding: 0.5em 1.25em;
  text-align: center;
  text-decoration: none;
  text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.8);
  vertical-align: middle;
  transition: all 0.27s ease-in-out;
}

.raised-card-project:hover {
  box-shadow: inset 0px 1px 0px rgba(255, 255, 255, 0.2),
    inset 0px -1px 0px rgba(255, 255, 255, 0.1),
    inset 1px 0px 0px rgba(255, 255, 255, 0.05),
    inset -1px 0px 0px rgba(255, 255, 255, 0.05), 0 14px 18px #0f3f4b;
  transition: all 0.27s ease-in-out;
}

.top-margin {
  margin-top: 3rem;
}

.flexbox_project {
  display: flex;
  align-items: center;
}

.btn_project {
  margin: 1rem;
  padding: 1rem;
  display: inline-block;
  color: #ff7b00;
  border: 0.08rem solid #006c80;
  padding: 1rem;
  transition: all 0.2s ease-in-out;
  border-radius: 0.28rem;
}

.btn_project:hover {
  /* border: 1px solid rgba(0, 0, 0, 0.342); */
  border-radius: 0.28rem;
  background-color: #006c80;
  color: #ff7b00;
  transition: all 0.2s ease-in-out;
}

.desc-font {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 300;
}

.project-img-shadow {
  box-shadow: 0px 1px 0px rgb(255 255 255 / 30%);
  border: 1px solid rgba(0, 0, 0, 0.9);
  border-radius: 0.18rem;
}

.para-space {
  padding: 0.63rem;
  margin: 0.63rem;
}

.img-width-gaialivsstil {
  max-width: 100%;
}

.inline {
  display: inline;
}

@media (max-width: 900px) {
  .flexbox_project {
    flex-wrap: wrap-reverse;
    flex-direction: column;
  }

  .ss-text {
    font-size: 16px;
  }

  .project-img-shadow {
    max-width: 78vw;
    width: 100%;
    margin: auto;
  }

  .para-space {
    margin: 3px;
    padding: 3px;
  }

  .raised-card-project {
    padding: 0.3rem;
  }
}
